  margin-top: auto;
  margin-left: 15px;
}
.weui-btn-area {
  margin: 1.17647059em 15px 0.3em;
}
.weui-btn-area_inline {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.weui-btn-area_inline .weui-btn {
  margin-top: auto;
  margin-right: 15px;
  width: 100%;
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
}
.weui-btn-area_inline .weui-btn:last-child {
  margin-right: 0;
}
.weui-loading {
  width: 20px;
  height: 20px;
  display: inline-block;
  vertical-align: middle;
  -webkit-animation: weuiLoading 1s steps(12, end) infinite;
          animation: weuiLoading 1s steps(12, end) infinite;
  background: transparent url("data:image/svg+xml;base64,") no-repeat;
  background-size: 100%;
}
.weui-loading.weui-loading_transparent {
  background-image: url("data:image/svg+xml;base64,");
}
@-webkit-keyframes weuiLoading {
0% {
    -webkit-transform: rotate3d(0, 0, 1, 0deg);
            transform: rotate3d(0, 0, 1, 0deg);
}
100% {
    -webkit-transform: rotate3d(0, 0, 1, 360deg);
            transform: rotate3d(0, 0, 1, 360deg);
}
}
@keyframes weuiLoading {
0% {
    -webkit-transform: rotate3d(0, 0, 1, 0deg);
            transform: rotate3d(0, 0, 1, 0deg);
}
100% {
    -webkit-transform: rotate3d(0, 0, 1, 360deg);
            transform: rotate3d(0, 0, 1, 360deg);
}
}
.weui-btn.vux-x-button-no-border:after {
  display: none;
}

.vux-fade-enter-active,
.vux-fade-leave-active {
  opacity: 1;
  -webkit-transition: opacity linear 0.2s;
  transition: opacity linear 0.2s;
}
.vux-fade-enter,
.vux-fade-leave-to {
  opacity: 0;
}
.vux-dialog-enter-active {
  -webkit-animation: vux-dialog-in 0.5s;
          animation: vux-dialog-in 0.5s;
}
.vux-dialog-leave-active {
  -webkit-animation: vux-dialog-out 0.3s;
          animation: vux-dialog-out 0.3s;
}
@-webkit-keyframes vux-dialog-in {
0% {
    -webkit-transform: scale(1.185);
            transform: scale(1.185);
    opacity: 0;
}
100% {
    -webkit-transform: scale(1);
            transform: scale(1);
    opacity: 1;
}
}
@keyframes vux-dialog-in {
0% {
    -webkit-transform: scale(1.185);
            transform: scale(1.185);
    opacity: 0;
}
100% {
    -webkit-transform: scale(1);
            transform: scale(1);
    opacity: 1;
}
}
@-webkit-keyframes vux-dialog-out {
0% {
    -webkit-transform: scale(1);
            transform: scale(1);
    opacity: 1;
}
100% {
    -webkit-transform: scale(0.85);
            transform: scale(0.85);
    opacity: 0;
}
}
@keyframes vux-dialog-out {
0% {
    -webkit-transform: scale(1);
            transform: scale(1);
    opacity: 1;
}
100% {
    -webkit-transform: scale(0.85);
            transform: scale(0.85);
    opacity: 0;
}
}
.vux-mask-enter,
.vux-mask-leave-active {
  opacity: 0;
}
.vux-mask-leave-active,
.vux-mask-enter-active {
  -webkit-transition: opacity 300ms;
  transition: opacity 300ms;
}
